import React from 'react'
import { useState } from 'react';
import { NavLink } from 'react-router-dom';
import classes from '../styles/Navigation.module.scss';
import logo from '../assets/shared/logo.svg';
import hamburegIcon from '../assets/shared/icon-hamburger.svg'
import closeIcon from '../assets/shared/icon-close.svg';

function Navigation() {
  const [isOpen, setIsOpen] = useState(false)
  const openMenu = () => {
    setIsOpen(!isOpen);
  }
  return (
    <nav>
      <div className={classes.logo}>
        <img src={logo} alt="" />
      </div>
      <div className={isOpen ? classes.menu : `${classes.menu} ${classes.menuClosed}`}>
        <div className={classes.toggle} onClick={openMenu}>
          {!isOpen && <img src={hamburegIcon} alt="" />}
          {isOpen && <img src={closeIcon} alt="" />}
        </div>
        <ul className={isOpen ? classes.menuOpen : classes.menuClosed}>
          <li>
            <NavLink to="/"><span>00</span>Home</NavLink>
          </li>
          <li>
            <NavLink to="/"><span>01</span>Destination</NavLink>
          </li>
          <li>
            <NavLink to="/"><span>02</span>Crew</NavLink>
          </li>
          <li>
            <NavLink to="/"><span>03</span>Technology</NavLink>
          </li>
        </ul>

      </div>
    </nav>
  )
}

export default Navigation
